body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #141414;
  color: white;
}

.App {
  text-align: center;
  margin-top: 20px;
}

h1 {
  color: #e50914;
  margin-bottom: 30px;
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 20px;
  padding: 20px 40px;
}

.card {
  cursor: pointer;
  text-align: center;
}

.card img {
  width: 100%;         /* riempie la card orizzontalmente */
  height: 200px;       /* altezza fissa per uniformare tutte le immagini */
  object-fit: cover;   /* ritaglia l'immagine senza deformarla */
  border-radius: 10px; /* angoli arrotondati */
  transition: transform 0.3s;
}

.card:hover img {
  transform: scale(1.05);
}

.card p {
  margin-top: 10px;
  color: #61dafb;
}